/* 
=====================================
=====================================
              GENERAL
=====================================
=====================================
*/

/* RESET
============================================= */
h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
a,
p,
span {
  padding: 0;
  margin: 0;
  user-select: none;
  list-style: none;
  text-decoration: none;
}

/* BODY
============================================= */
body {
  font-family: 'Titan One', 'DM Sans', sans-serif;
}

body::-webkit-scrollbar {
  display: none;
}

/* CONTAINER (CENTERING AND WIDTH)
============================================= */
.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include tablet {
    max-width: 768px;
  }
  @include desktop {
    max-width: 1280px;
  }
}

/* SECTION (TOP AND BOTTOM PADDING)
============================================= */
.section {
  padding: 60px 20px;
}

/* HIDDING ELEMENT 
============================================= */
.is-hidden {
  @include mobile {
    display: none;
  }
  @include tablet {
    &--tablet {
      display: none;
    }
  }
  @include desktop {
    display: flex;
  }
}

.title {
  display: flex;
  flex-direction: column;
  gap: 15px;

  text-align: center;
  &--small {
    color: getColor('full-pinky');
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;
    @include tablet {
      font-size: 26px;
      line-height: 1.15;
    }
    @include desktop {
      font-size: 30px;
      line-height: 1.13;
      letter-spacing: 0.04em;
    }
  }
  &--big {
    color: getColor('dark-pink');
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    @include tablet {
      font-size: 48px;
      line-height: 1.145;
    }
    @include desktop {
      font-size: 58px;
      line-height: 1.14;
      letter-spacing: 0.06em;
    }
  }
}

.button {
  border-radius: 20px;
  border: none;
  font-family: 'DM Sans';

  font-weight: 700;
  font-size: 16px;
  line-height: 1.3;
  padding: 10px 37.23px;
  @include tablet {
  }
  &--small {
    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;
    padding: 7px 14.5px;
    @include desktop {
      font-size: 14px;
      line-height: 1.28;
      padding: 8px 19px;
    }
  }
  &--red {
    background-color: getColor('dark-pink');
    color: getColor('semi-white');
  }
  &--white {
    background-color: getColor('semi-white');
    color: getColor('dark-pink');
  }
}
